export default {
	data() {
		return {
			wiffInfo: {
				BSSID: '',
				SSID: '',
				PASSWORD: '',
				isOk: true
			}
		}
	},
	onLoad(option) {
		uni.$on('changeMsg', (val) => {
			this.$store.dispatch('a_getCity')
		})
		
	},
	methods: {
		getMessage(obj) {
			this.msg = obj
		},
		handleWiff() {
			let _this = this
			wx.getConnectedWifi(
				{
					success(res) {
						console.log(res)
					},
					fail(err) {
						uni.showToast({
							title: '请先链接wiff',
							icon: 'none'
						})			
					}
				}
			)
		},
		handleConfigWiff(flag) {
			if(flag) {
				uni.showToast({
					title: '配网成功',
					icon: 'none'
				})
					console.log(this.wiffInfo)
				this.wiffInfo.isOk = false
			} else {
				uni.showToast({
					title: '配网失败，请重新获取wiff信息，输入密码',
					icon: 'none'
				})	
				this.wiffInfo.SSID = ''
				this.wiffInfo.BSSID = ''
				this.wiffInfo.PASSWORD = ''
			}
		}
	}
}
